.page-heading,
.page-contents {
  display: grid; }

.page-contents {
  grid-template-columns: minmax(360px, 720px);
  grid-template-areas: "content-1" "content-2";
  grid-column-gap: 1rem;
  grid-row-gap: 2rem; }

.content-1 {
  grid-area: content-1; }

.content-2 {
  grid-area: content-2; }

div[class*="content-"] {
  display: flex;
  flex-direction: column; }

div[class*="content-"] > div {
  min-height: 4em;
  justify-content: left;
  margin: 1px 0;
  background: rgba(128, 128, 128, 0.65);
  width: 100%; }

.caption-icon {
  font-size: 2.5rem;
  display: inline-flex; }

.detail-list {
  display: flex;
  flex-direction: column; }

.detail-field {
  align-items: center; }

.detail-field .field-label {
  min-width: 140px;
  width: 140px; }

.detail-field .field-value {
  flex: 1; }

.product-details {
  display: flex;
  width: 100%;
  flex-direction: column;
  gap: 1em; }

.product-details > button {
  align-self: flex-start; }

.product-item {
  border: 1px solid #fff; }

.product-name {
  overflow: hidden;
  text-overflow: ellipsis;
  width: inherit; }

.productdlg-item {
  flex-wrap: wrap; }

.productdlg-item > div {
  text-transform: capitalize; }

div[class*="attr-"] {
  flex-basis: 100%; }

.search-field {
  width: 100%; }

.search-field {
  width: 100%;
  overflow: hidden;
  word-wrap: none;
  text-overflow: ellipsis; }

.ctext-placed {
  color: #83c7ff; }

.ctext-accepted {
  color: #5284e0; }

.ctext-cancelled {
  color: #bd1313; }

.ctext-replaced {
  color: #6008b3; }

.ctext-preparing {
  color: #f8a251; }

.ctext-finalizing {
  color: #e9a616; }

.ctext-out-for-delivery {
  color: #06c2b2; }

.ctext-ready-for-pickup {
  color: #06c2b2; }

.ctext-fulfilled {
  color: #47b87f; }

@media (max-width: 580px) {
  .detail-field {
    flex-direction: column;
    align-items: flex-start; }
  .detail-field .field-label,
  .detail-field .field-value {
    line-height: 2em;
    width: 100%; } }

@media (max-width: 810px) {
  .product-field {
    flex-direction: column;
    align-items: flex-start; }
  .product-field .product-details {
    line-height: 2em;
    width: 100%; } }
